// jobs-3
.jobs {
    &__tab {
        &_btns {
            display: flex;
            flex-wrap: wrap;
            gap: 10px;
            margin-bottom: 70px;
        }

        &_btn {
            border: 1px solid var(--color-zelenyy);
            border-radius: 10px;
            padding: 10px 20px;
            color: var(--color-zelenyy);
            &.tab-active {
                background: var(--color-zelenyy);
                color: var(--belyy);
            }
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        &_name {
            font-weight: 700;
            font-size: 40px;
            line-height: 120%;
            color: var(--tekst);
            padding-bottom: 40px;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            margin-bottom: 40px;
        }

        &_blocks {
            margin-bottom: 40px;
        }

        &_block {
            display: flex;
            gap: 40px;
            &:not(:last-child) {
                padding-bottom: 30px;
                border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
                margin-bottom: 30px;
            }

            & ul {
                display: flex;
                flex-direction: column;
                gap: 12px;
                & li {
                    display: flex;
                    gap: 10px;
                    &::before {
                        content: "";
                        display: block;
                        min-width: 18px;
                        width: 18px;
                        height: 3px;
                        border-radius: 10px;
                        background: var(--plashki);
                        margin-top: 7px;
                    }
                }
            }
        }

        &_block-main {
            flex: 1 0 65%;
        }

        &_block-name {
            flex: 1 0 15%;
            font-weight: 700;
            color: var(--tekst);
        }

        &_content {
            display: contents;
        }

        &_labels {
            display: flex;
            flex-wrap: wrap;
            gap: 10px;
        }

        &_label {
            flex: 1 0 20%;
            display: flex;
            flex-direction: column;
            gap: 22px;
            border: 1px solid var(--color-svetlo-zelenyy-2);
            border-radius: 10px;
            padding: 24px;
            & p:first-child {
                font-weight: 500;
                color: var(--tekst);
            }
            & p:last-child {
                font-size: 14px;
            }
        }

        &_bottom {
            border-radius: 10px;
            padding: 30px 40px;
            background: linear-gradient(210deg, #6aa5ae 0%, #3d7c85 100%);
            display: flex;
            align-items: center;
            gap: 40px;
        }

        &_text {
            flex: 1 0 50%;
            font-weight: 700;
            font-size: 18px;
            line-height: 140%;
            color: var(--belyy);
        }

        &_contacts {
            flex: 1 0 40%;
            display: flex;
            flex-direction: column;
            gap: 6px;
            color: #ebebeb;
            & a {
                text-wrap: nowrap;
                font-weight: 500;
                color: var(--belyy);
            }
        }
    }
}



// media jobs-3
@media (max-width: 1040px) {
    .jobs {
        &__item {
            &_label {
                flex: 1 0 40%;
            }
        }
    }
}
@media (max-width: 740px) {
    .jobs {
        &__tab {
            &_btns {
                margin-bottom: 30px;
            }
        }
        &__list {
            gap: 30px;
        }
        &__item {
            &_name {
                font-size: 18px;
                padding-bottom: 22px;
                margin-bottom: 26px;
            }
            &_blocks {
                margin-bottom: 20px;
            }
            &_labels {
                flex-direction: column;
                gap: 10px;
            }
            &_label {
                flex-direction: row;
                align-items: center;
                gap: 14px;
                padding: 16px 20px;
            }
            &_block {
                flex-direction: column;
                gap: 10px;
                &:nth-child(n) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
                    margin-bottom: 26px;
                }
                &:last-child {
                    margin-bottom: 0;
                }
                & ul li::before {
                    min-width: 12px;
                    width: 12px;
                    height: 2px;
                    margin-top: 8px;
                }
            }
            &_block-name {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 10px;
                &::after {
                    content: "";
                    display: inline-block;
                    width: 8px;
                    height: 8px;
                    border-top: 1px solid var(--color-temno-zelenyy);
                    border-right: 1px solid var(--color-temno-zelenyy);
                    transform: rotate(135deg);
                    margin-left: 8px;
                    transition: all 0.3s ease;
                }
                &.accordion--active {
                    &::after {
                        transform: rotate(315deg);
                    }
                }
            }
            &_content {
                display: block;
                max-height: 0;
                overflow: hidden;
            }
            &_bottom {
                border-radius: 10px;
                padding: 20px;
                flex-direction: column;
                align-items: stretch;
                gap: 14px;
            }
            &_text {
                font-size: 16px;
            }
        }
    }
}
