// page-stocks-4
.page-stocks {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #c7d6dc;
        margin-bottom: 20px;
    }

    &__tab {
        &_btns {
            border: 1px solid var(--color-svetlo-zelenyy-2);
            border-radius: 7px;
            display: flex;
        }

        &_btn {
            border-radius: 5px;
            padding: 16px 20px;
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: var(--tekst-svetlyy);
            border: 1px solid transparent;
            &.tab__btn--active {
                border: 1px solid var(--color-svetlo-zelenyy-1);
                background: var(--color-svetlo-zelenyy-2);
                color: var(--tekst);
            }
        }
    }

    &__quantity {
        font-size: 14px;
        text-align: right;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__card {
        display: flex;
        flex-direction: column;
        gap: 4px;

        &_top {
            border-radius: 20px;
            height: 200px;
            position: relative;
            overflow: hidden;
        }

        &_picture {
            position: absolute;
            width: 100%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            &::after {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 2;
                width: 100%;
                height: 100%;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.76) 100%);
            }
        }

        &_title {
            width: 100%;
            height: 100%;
            padding: 24px;
            position: relative;
            z-index: 3;
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--belyy);
            display: flex;
            align-items: end;
        }

        &_inner {
            border: 1px solid var(--color-svetlo-zelenyy-1);
            border-radius: 20px;
            padding: 24px;
        }

        &_text {
            margin-bottom: 20px;
        }

        &_date {
            padding-bottom: 10px;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            margin-bottom: 10px;
            & p {
                border-radius: 5px;
                padding: 6px 10px;
                background: var(--color-svetlo-zelenyy-2);
                font-weight: 500;
                font-size: 12px;
                color: var(--color-zelenyy);
                width: fit-content;
            }
        }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }

        &_btn-more {
            font-weight: 500;
            font-size: 14px;
            color: var(--color-zelenyy);
        }
    }
}


// media page-stocks-3
@media (max-width: 1100px) {
    .page-stocks {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 700px) {
    .page-stocks {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
        &__card {
            &_top {
                min-height: 150px;
                height: auto;
            }
            &_title {
                padding: 16px;
                font-size: 18px;
            }
            &_inner {
                padding: 16px;
            }
        }
    }
}
@media (max-width: 520px) {
    .page-stocks {
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 26px;
        }
        &__tab {
            &_btns {
                flex-direction: column;
                gap: 0;
                border-radius: 5px;
                padding: 6px;
                opacity: 0.7;
            }
        }
        &__quantity {
            text-align: left;
        }
        &__card {
            &_bottom {
                flex-direction: column;
                align-items: stretch;
            }
            &_btn-more {
                text-align: center;
            }
        }
    }
}


