// doctor-page-1

.doctor-page {
    &__grid {
        display: grid;
        grid-template-columns: 3fr 6fr;
        gap: 40px;
        align-content: normal;
    }

    &__photo {
        width: 100%;
        max-width: 550px;
        position: relative;
        margin-bottom: 30px;
    }

    &__img {
        width: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 10px;
        }
    }

    &__rating {
        position: absolute;
        bottom: 16px;
        left: 16px;
        right: 16px;
        border-radius: 5px;
        padding: 6px 14px;
        background: var(--bg-svetlo-seryy);
        display: flex;
        align-items: center;
        justify-content: center;

        &_estimation {
            padding-right: 16px;
            border-right: 1px solid #c7d6dc;
            margin-right: 16px;
            font-weight: 700;
            font-size: 20px;
            color: var(--color-temno-zelenyy);
            display: flex;
            gap: 5px;
        }

        &_stars {
            display: flex;
            gap: 2px;
        }
        &_star {
            opacity: 0.6;
        }
    }

    &__prices {
        display: flex;
        gap: 10px;
        margin-bottom: 10px;

        & p {
            border-radius: 5px;
            padding: 14px 20px;
            background: rgba(199, 214, 220, 0.4);
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst);
            text-align: center;
            flex: 1 0 48%;
            & span {
                font-weight: 700;
                font-size: 16px;
                color: var(--tekst-svetlyy);
            }
        }
    }

    &__btn {
        width: 100%;
    }

    &__btns {
        margin-bottom: 40px;
    }

    &__schedule {
        border-radius: 10px;
        padding: 30px;
        background: #f9fafb;
        &_title {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            margin-bottom: 30px;
        }
        &_item {
            color: var(--tekst);
            display: flex;
            justify-content: space-between;
            gap: 20px;
            padding: 14px 0px;
            border-bottom: 1px solid #c7d6dc;
            &:last-child {
                border-bottom: none;
            }
        }
        &_time {
            font-weight: 500;
        }
        &_weekend {
            font-weight: 500;
            color: var(--color-zelenyy);
        }
    }

    &__right {
        border-radius: 20px;
        padding: 40px;
        background: #f9fafb;
        display: flex;
        flex-direction: column;
        gap: 50px;

        &_title {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            margin-bottom: 20px;
        }
    }

    &__content {
        display: contents;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 30px;

        &_experience {
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--belyy);
            font-weight: 500;
            font-size: 14px;
            color: var(--color-temno-zelenyy);
            width: fit-content;
            margin-bottom: 10px;
        }

        &_fio {
            font-weight: 700;
            font-size: 40px;
            line-height: 120%;
            color: var(--tekst);
        }

        &_post {
            display: flex;
            gap: 30px;
        }
    }

    &__post {
        &_item {
            font-size: 14px;
            line-height: 140%;
            display: flex;
            gap: 10px;
            &:first-child {
                padding-right: 30px;
                border-right: 1px solid #c7d6dc;
            }
        }
        &_icon {
            display: block;
            border-radius: 6px;
            padding: 8px;
            min-width: 43px;
            width: 43px;
            height: 43px;
            background: var(--belyy);
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &_text {
            margin-bottom: 5px;
        }
        &_post {
            font-weight: 500;
            color: var(--tekst);
        }
    }

    &__tab {
        &_btns {
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            margin-bottom: 30px;
        }
        &_btn {
            border-radius: 5px;
            padding: 8px 16px;
            background: var(--belyy);
            font-weight: 500;
            font-size: 14px;
            transition: all 0.3s ease;
            &:hover {
                background: var(--color-svetlo-zelenyy-2);
            }
            &.active {
                background: var(--color-svetlo-zelenyy-1);
                color: var(--tekst);
            }
        }
    }

    &__list ul {
        display: flex;
        flex-direction: column;
        gap: 20px;
        & li {
            display: flex;
            gap: 12px;
            color: var(--tekst);
            &::before {
                content: "";
                display: block;
                border-radius: 10px;
                min-width: 18px;
                width: 18px;
                height: 3px;
                background-color: var(--color-zelenyy);
                margin-top: 9px;
            }

            & span {
                font-weight: 700;
                color: var(--color-zelenyy);
            }
        }
    }

    &__diplomas {
        &_swiper {
            max-width: 773px;
            margin-bottom: 20px;
            height: auto;
        }
        &_swiper-slide {
            border-radius: 10px;
            overflow: hidden;
            background: #f2f4f7;
            & a {
                display: block;
                border-radius: 10px;
                overflow: hidden;
                & p {
                    padding: 20px;
                    width: 100%;
                    color: var(--tekst);
                    transition: all 0.8s ease;
                }
            }
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 10px;
            }
        }
        &_swiper-pagination {
            justify-content: center;
        }
        &_picture {
            display: block;
            border-radius: 10px;
            overflow: hidden;
        }
    }
}


// media doctor-page
@media (max-width: 1300px) {
    .doctor-page {
        &__grid {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__photo {
            margin: 0 auto;
            order: -3;
        }
        &__btns {
            order: -2;
            margin-bottom: 0;
        }
        &__right, &__left {
            display: contents;
        }
        &__info {
            order: -1;
        }
        &__content {
            display: flex;
            flex-direction: column;
            gap: 60px;
        }
        &__info, &__content {
            border-radius: 10px;
            padding: 30px;
            background: #f9fafb;
        }
        &__diplomas {
            &_swiper {
                max-width: 100%;
            }
        }
    }
}
@media (max-width: 688px) {
    .doctor-page {
        &__photo {
            max-width: 350px;
        }
        &__prices {
            & p {
                padding: 8px 20px;
                & span {
                    display: block;
                    font-size: 16px;
                }
            }
        }
        &__schedule {
            border-radius: 10px;
            padding: 20px;
            &_title {
                font-size: 18px;
                margin-bottom: 14px;
            }
            &_item {
                padding: 12px 0px;
            }
        }
        &__right {
            &_title {
                font-size: 18px;
                margin-bottom: 20px;
            }
        }
        &__info, &__content {
            border-radius: 10px;
            padding: 20px;
        }
        &__content {
            gap: 30px;
        }
        &__info {
            gap: 20px;
            &_fio {
                font-size: 18px;
            }
            &_post {
                flex-direction: column;
                gap: 12px;
            }
        }
        &__post {
            &_item:first-child {
                padding-right: 0;
                border-right: none;
                padding-bottom: 12px;
                border-bottom: 1px solid #c7d6dc;
            }
            &_icon {
                padding: 6px;
                min-width: 30px;
                width: 30px;
                height: 30px;
            }
        }
    }
}

