// article-2
.article {
    &__grid {
        display: grid;
        grid-template-columns: 4fr 8fr;
        gap: 60px;
        &:not(:last-child) {
            margin-bottom: 60px;
        }
    }

    & h2, & h3, & h4, & h5, & h6 {
        font-weight: 700;
        line-height: 140%;
        color: var(--tekst);
        margin-bottom: 20px;
    }
    & h2 {
        font-size: 34px;
    }
    & h3 {
        font-size: 30px;
    }
    & h4 {
        font-size: 26px;
    }
    & h5 {
        font-size: 18px;
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty, & h6:empty {
        margin-bottom: 0;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    & span {
        font-weight: 500;
    }
    & a:not(.phone-btn-white) {
        font-weight: 500;
        text-decoration: underline;
        color: var(--tekst-svetlyy);
        transition: all 0.3s ease;
        &:hover {
            color: var(--color-zelenyy);
        }
    }

    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;
        & li {
            display: flex;
            gap: 10px;
            &::before {
                content: "";
                display: block;
                min-width: 10px;
                width: 10px;
                height: 10px;
                border-radius: 100%;
                background-color: var(--color-zelenyy);
                margin-top: 7px;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;
        list-style-type: none;
        counter-reset: item;
        & li {
            display: flex;
            gap: 10px;
            &::before {
                content: counter(item);
                counter-increment: item;
                display: block;
                min-width: 28px;
                width: 28px;
                height: 28px;
                border: 1px solid var(--tekst-svetlyy);
                border-radius: 20px;
                font-weight: 500;
                font-size: 12px;
                color: var(--tekst-svetlyy);
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    &__img {
        & img {
            border-radius: 10px;
            width: 100%;
            object-fit: cover;
            margin-bottom: 10px;
        }
        & p {
            font-size: 14px;
            color: rgba(64, 95, 106, 0.8);
            margin: 0;
        }
    }

    &__form {
        position: relative;
        border-radius: 20px;
        padding: 30px;
        background: linear-gradient(210deg, #6aa5ae 0%, #3d7c85 100%);
        color: var(--belyy);
        overflow: hidden;
        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 120%;
            margin-bottom: 30px;
        }

        &_bg {
            position: absolute;
            top: 30px;
            right: -78px;
        }
    }


    & table {
        display: block;
        width: 100%;
        border: none;
        border-collapse: collapse; 
        margin-bottom: 20px;

        & thead {
            & th {
                border: none;
                padding: 20px 25px;
                background: var(--color-svetlo-zelenyy-2);
                font-weight: 500;
                font-size: 14px;
                color: var(--tekst);
                border-right: 1px solid 1px solid rgba(210, 220, 232, 0.5);
            }
            & tr th:first-child {
                border-radius: 10px 0 0 0;
            }
            & tr th:last-child {
                border-right: none;
                border-radius: 0 10px 0 0;
            }
        }

        & tbody {
            & td {
                border-right: 1px solid #f2f8f7;
                border-bottom: 1px solid #f2f8f7;
                padding: 20px;
                font-size: 14px;
                color: var(--tekst-svetlyy);
                vertical-align: top;
            }
            & tr td:last-child {
                border-right: none;
            }
            & tr:last-child td {
                border-bottom: none;
            }
        }
    }

    &__video {
        position: relative;
        width: 100%;
        border-radius: 10px;
        overflow: hidden;
        & video {
            width: 100%;
            display: block;
        }
        &_play {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            z-index: 10;
            transition: all 0.3s ease;
            &:hover {
                transform: translate(-50%, -50%) scale(1.1);
            }
        }
    }


    &__literature {
        margin-top: 50px;
        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 30px;
        }

        & ol {
            display: flex;
            flex-direction: column;
            gap: 10px;
            margin-bottom: 40px;
            list-style-type: none;
            counter-reset: item;
            & li {
                display: flex;
                gap: 12px;
                color: var(--tegi);
                opacity: 0.85;
                &::before {
                    content: counter(item);
                    counter-increment: item;
                    display: block;
                    min-width: max-content;
                    border: none;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 140%;
                    color: var(--color-temno-zelenyy);
                    opacity: 0.4;
                }
            }
        }

        &_bottom {
            display: flex;
            gap: 20px;
        }

        &_item {
            flex: 1 0 40%;
            border-radius: 10px;
            padding: 20px;
            background: #eff0f2;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);
            display: flex;
            gap: 15px;
            justify-content: space-between;
            & p {
                margin: 0 !important;
            }
        }

        &_links {
            display: flex;
            gap: 6px;
        }

        &_link {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            border-radius: 5px;
            background-color: var(--color-zelenyy);
        }
    }
}



// media article-2
@media (max-width: 1100px) {
    .article {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 0;
        }
        &__right {
            display: contents;
        }
        &__left {
            display: contents;
        }
        & h2, & h3, & h4, & h5, & h6 {
            order: -2;
        }
        &__img {
            order: -1;
            margin-bottom: 20px;
            & img {
                max-height: 400px;
            }
        }
        &__form {
            order: -3;
            margin-bottom: 40px;
        }
    }
}
@media (max-width: 688px) {
    .article {
        &__grid {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
        & h2 {
            font-size: 24px;
        }
        & h3 {
            font-size: 22px;
        }
        & h4 {
            font-size: 20px;
        }
        & h5 {
            font-size: 16px;
        }
        & ul li {
            &::before {
                min-width: 8px;
                width: 8px;
                height: 8px;
            }
        }
        &__img {
            & img {
                max-height: 230px;
            }
            & p {
                font-size: 12px;
            }
        }
        &__form {
            border-radius: 10px;
            padding: 20px;
            &_title {
                font-size: 22px;
                margin-bottom: 20px;
            }
        }
        & table {
            overflow-x: auto;
            max-width: 100%;
            & thead th, & tbody td {
                min-width: 243px;
            }
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }
        &__literature {
            margin-top: 30px;
            &_title {
                font-size: 20px;
                margin-bottom: 20px;
            }
            & ol {
                margin-bottom: 17px;
                & li {
                    font-size: 12px;
                    &::before {
                        font-size: 14px;
                    }
                }
            }
            &_bottom {
                flex-direction: column;
                gap: 12px;
            }
            &_item {
                padding: 16px;
                font-size: 14px;
            }
        }
    }
}