// licenses-2
.licenses-page__inner-2 {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}


.licenses-page__info-2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    border-bottom: 1px solid var(--decor-gray);
    padding-bottom: 30px;
    margin-bottom: 30px;

    & p {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.licenses-page__info-wrap-2 {
    flex-basis: calc(55% - 15px);
}

.licenses-page__picture-2 {
    max-height: 430px;
    border-radius: 10px;

    & img {
        width: 100%;
        height: 100%;
        border-radius: 10px;
        max-height: 430px;
        object-fit: cover;
    }
}

.licenses-page__container-2 {
    display: flex;
    flex-direction: column;
}

.licenses-page__label-2 {
    flex-basis: calc(45% - 15px);
    font-weight: 500;
    line-height: 140%;
    color: #fff;
    text-align: start;
    background: var(--gradient-green);
    padding: 30px;
    border-radius: 10px;
    height: auto;
}

.licenses-page__item-2.img-animation {
    border-radius: 10px;
}

.licenses-page__item-2.img-animation:before {
    border-radius: 10px !important;
}

.licenses-page__swiper-btns-2 {
    display: none;
}

.licenses-page__item-2 {
    border-radius: 10px;
    max-height: 430px;

}

.licenses-page__item-wrap-2 {
    overflow: hidden;
    border-radius: 10px;
    margin-bottom: 5px;
}

.licenses-page__slide-label-2 {
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--color-svetlo-zelenyy-2);
    border: 1px solid var(--color-svetlo-zelenyy-1);
    padding: 15px;
    border-radius: 10px;
    line-height: 120%;
    z-index: 2;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: var(--tekst-svetlyy);
    font-size: 14px;
}

.licenses-page__slide-2 {
    border-radius: 10px;
    overflow: hidden;
}

.licenses-page__info-link-2 {
    position: relative;
    padding-right: 20px;
    color: #fff;
    max-width: fit-content;
    transition: opacity .3s ease;

    &:before {
        content: '';
        position: absolute;
        right: 0;
        top: 2px;
        width: 14px;
        height: 14px;
        background: url("./../img/help/lic-download-icon.svg") no-repeat center center / contain;
    }

    &:hover {
        opacity: .6;
    }
}

.licenses-page__info-link__view-2 {
    position: relative;
    padding-right: 25px;
    color: #fff;
    max-width: fit-content;
    transition: opacity .3s ease;

    &:before {
        content: '';
        position: absolute;
        right: 0;
        top: 3px;
        width: 18px;
        height: 13px;
        background: url("./../img/help/lic-eye-icon.svg") no-repeat center center / contain;
    }

    &:hover {
        opacity: .6;
    }
}

.licenses-page__info-links-2 {
    display: flex;
    gap: 20px;
    justify-content: space-between;
}

.licenses-page__container-2 {
    padding: 0;
}

// media licenses-2

@media (max-width: 1380px) {
    .licenses-page__swiper-2 {
        padding: 0 20px;
    }

    .licenses-page__info-2 {
        margin: 0 20px 20px 20px;
    }
}

@media (max-width: 1100px) {
    .licenses-page__info-2 {
        flex-direction: column;
    }

    .licenses-page__label-2 {
        flex-basis: 100%;
        width: 100%;
    }

    .licenses-page__info-wrap-2 {
        flex-basis: 100%;
    }

    .licenses-page__info-2 {
        gap: 20px;
    }
}

@media (max-width: 650px) {
    .licenses-page__swiper-btns-2 {
        display: flex;
        width: 100%;
        margin: 0 20px;
    }

    .licenses-page__slide-label-2 {
        padding: 12px;
    }

    .licenses-page__label-2 {
        padding: 20px;
    }

    .licenses-page__info-2 {
        padding-bottom: 20px;
        margin-bottom: 20px;
    }

    .licenses-page__info-links-2 {
        flex-wrap: wrap;
        gap: 10px;

        &>* {
            font-weight: 400;
        }
    }
}