// gallery-4
.gallery {

    &__inner {
        position: relative;
    }

    &__nav {
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 10;
        transform: translate(-50%, 50%);
        display: flex;
        gap: 40px;
        border-radius: 60px;
        padding: 10px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
        background: var(--belyy);
        width: 60%;

        &_btns {
            width: 100%;
        }
    }

    &__swiper-scrollbar {
        width: 100% !important;
    }

    &__swiper-slide {
        height: auto;
    }

    &__tab {
        &_btns {
            display: flex;
            justify-content: space-between;
            gap: 15px;
            width: 100%;
            margin-bottom: 10px;
        }
        &_btn {
            display: block;
            font-size: 14px;
            color: var(--color-zelenyy);
            transition: all 0.3s ease;
            &::before {
                content: "";
                display: inline-block;
                border-radius: 100%;
                width: 9px;
                height: 9px;
                border: 1px solid var(--color-zelenyy);
                margin-right: 4px;
                vertical-align: middle;
            }
            &:hover {
                -webkit-text-stroke: 0.5px var(--color-temno-zelenyy);
            }
        }

        &_btn.tab__btn--active {
            -webkit-text-stroke: 0.8px var(--color-temno-zelenyy);
            color: var(--color-temno-zelenyy);
            &::before {
                background-color:var(--color-zelenyy);
            }
        }
    }

    &__image {
        display: block;
        border-radius: 20px;
        overflow: hidden;
        position: relative;
        width: 100%;
        height: 100%;
        max-height: 600px;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__label {
        position: absolute;
        top: 20px;
        left: 20px;
        right: 20px;
        z-index: 5;
        border-radius: 20px;
        padding: 10px 30px;
        background: var(--belyy);
        font-weight: 500;
        font-size: 14px;
        color: var(--color-zelenyy);
        width: fit-content;
    }
}



// media gallery-4
@media (max-width: 1100px) {
    .gallery {
        &__inner {
            margin-bottom: 30px;
        }
        &__nav {
            position: absolute;
            bottom: 0;
            left: 0;
            transform: translate(0, 50%);
            display: flex;
            gap: 40px;
            width: 100%;
        }
    }
}
@media (max-width: 688px) {
    .gallery {
        overflow: hidden;
        &__inner {
            display: flex;
            flex-direction: column-reverse;
            gap: 20px;
            margin-bottom: 10px;
        }
        &__nav {
            position: static;
            transform: translate(0, 0);
            border-radius: 10px;
            &_btns {
                max-width: calc(100% - 160px);
            }
        }
        &__tab {
            &_btns {
                gap: 40px;
                overflow-x: auto;
                &::-webkit-scrollbar {
                    width: 0;
                    height: 0;
                }
            }
            &_btn {
                display: block;
                font-size: 16px;
                font-weight: 500;
                min-width: max-content;
            }
        }
        &__swiper {
            overflow: visible;
        }
        &__image {
            border-radius: 10px;
            max-height: 380px;
            min-height: 300px;
        }
        &__label {
            top: auto;
            bottom: 8px;
            left: 10px;
            right: 10px;
            border-radius: 5px;
            padding: 6px 8px;
            font-size: 12px;
        }
    }
}