// doctors-page-5
.doctors-page {
    &__top {
        margin-bottom: 40px;
    }

    &__sort {
        display: flex;
        gap: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
        margin-bottom: 20px;

        &_block {
            flex: 1 0 30%;
        }

        & select {
            height: 100%;
        }
    }

    &__search {
        height: 100%;
        & input {
            height: 100%;
        }
    }

    &__tab {
        &_btns {
            display: flex;
            border-radius: 5px;
            padding: 6px;
            background: var(--fon-plashek);
            height: 100%;
        }
        &_btn {
            flex: 1 0 40%;
            display: block;
            border-radius: 5px;
            padding: 15px 20px;
            font-weight: 500;
            color: var(--tekst);
            opacity: 0.7;
            &:hover {
                opacity: 1;
            }
            &.tab__btn--active {
                color: var(--color-temno-zelenyy);
                box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.08);
                background: var(--belyy);
                opacity: 1;
            }
        }
    }

    &__quantity {
        font-size: 14px;
    }

    &__cards .tabcontent--active {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    &__card {
        display: flex;
        gap: 10px;

        &_photo {
            flex: 1 0 32%;
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 10px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }
        }

        &_inner {
            flex: 1 0 50%;
            border-radius: 10px;
            padding: 28px;
            background: var(--bg-svetlo-seryy);
        }

        &_experience {
            border-radius: 5px;
            padding: 10px 16px;
            background: var(--color-svetlo-zelenyy-2);
            font-weight: 500;
            font-size: 14px;
            color: var(--tekst-svetlyy);
            width: fit-content;
            margin-bottom: 12px;
        }

        &_fio {
            font-weight: 700;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 12px;
        }

        &_post {
            font-size: 14px;
            line-height: 140%;
            margin-bottom: 30px;
            & p:not(:last-child) {
                padding-bottom: 8px;
                border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
                margin-bottom: 8px;
            }
        }

        &_btns {
            display: flex;
            gap: 6px;
            & button {
                flex: 1 0 auto;
            }
        }
    }
}


// media doctors-page-5
@media (max-width: 1100px) {
    .doctors-page {
        &__card {
            flex-direction: column;
            &_photo {
                flex: 0 1 auto;
                max-height: 320px;
            }
            &_inner {
                flex: 0 1 auto;
            }
        }
    }
}
@media (max-width: 900px) {
    .doctors-page {
        &__top {
            margin-bottom: 20px;
        }
        &__sort {
            flex-direction: column;
            gap: 12px;
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 30px;
        }
    }
}
@media (max-width: 688px) {
    .doctors-page {
        &__card {
            position: relative;
            &_photo {
                max-height: 260px;
            }
            &_inner {
                position: relative;
                padding: 20px;
            }
            &_experience {
                padding: 6px 10px;
                background: rgba(190, 219, 215, 0.8);
                margin-bottom: 0;
                position: absolute;
                transform: translate(-10px, -70px);
            }
            &_fio {
                font-size: 18px;
            }
            &_post {
                margin-bottom: 20px;
            }
            &_btns {
                flex-direction: column;
            }
        }
    }
}
@media (max-width: 550px) {
    .doctors-page {
        &__cards .tabcontent--active {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
        &__tab {
            &_btns {
                flex-direction: column;
            }
            &_btn {
                padding: 10px 16px;
            }
        }
    }
}

